

import doctorOrOrg from '../../components/DoctorOrOrg';
import moduleTitle from '../../components/ModuleTitle';
import tools from '../../lib/tools.js';
import Toast from 'vux/src/components/toast/index.vue'

import BScroll from 'better-scroll'
export default {
name:'orgList',
components:{doctorOrOrg,moduleTitle,Toast},
data(){
return {
id:'',
showToast:false,
toastMsg:'',
tempService:[],
pageNumber:1,
pageSum:0,
container:null,
dragging: false,
times: 0,
loading: false,
loadable:false,
pullUpObj:{
type:0,
text:'松开立即加载'
},// 0 初始化 1可以加载 2最后一页
}
},
mounted(){
this.init();
},
beforeDestory() {
},
methods:{
init(){
var vm = this;
this.$http.axios({method:'POST',url:'/jyOrgInfo/getJyOrgInfoList',data:{showCount:10,currentPage:this.pageNumber,type:4}}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
var tempArr = [];
for(let i=0;i<data.items.length;i++){
let item = data.items[i];
tempArr.push({src:item.imageUrl,title:item.name,desc:item.address,url:'/m/familyAllDoctor.html?orgId='+item.managerId+'&orgName='+item.name+'&cityCodeDesc='+item.townCodeDesc});
}
this.pageSum = data.totalItemsCount;
this.loading = false;
this.loadable = (this.pageSum>1);
if(this.pullUpObj.type==0){
vm.tempService = tempArr;
vm.pullUpObj.type =1;
}else{
vm.tempService = [...vm.tempService,...tempArr];
}
}else{
this.loading = false;
this.loadable = false;
console.log(data.resultCode);
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
}
},(error) => {
this.loading = false;
this.loadable = false;
console.log(error);
vm.showToast = true;
vm.toastMsg = '操作失败';
})
},
nextPage(){
if(!(this.pageSum==this.times)){
this.times += 1;
this.pageNumber += 1;
console.log('nextPage');
this.init();
}else{
this.pullUpObj.type = 2;
this.pullUpObj.text = '已经到底了'
}
},
reLoad(){
this.pullUpObj = {
type:0,
text:'松开立即加载'
};
this.times = 0;
this.pageNumber = 1;
this.init();
}
}
}



